$bun-color: #DBCB7A;
$patty-color: #A06640;

$burger-border: 6px solid rgba(0,0,0,0.2);

.burger {
    --burger-width: 20vw;

    display: inline-block;
    width: var(--burger-width);
    max-width: 100%;
    max-height: 100% !important;
    z-index: 100;
    text-align: center;

    .burger-item {
        border: $burger-border;
        display: inline-block;
        width: 95%;
    }

    &__bun {
        background-color: $bun-color;
        width: 100% !important;
        height: calc(#{var(--burger-width)} / 4);
        box-shadow: inset -15px 0 rgba(0,0,0,0.1);

        &.top {
            border-top-left-radius: 130px;
            border-top-right-radius: 130px;
        }

        &.bottom {
            border-bottom-left-radius: 130px;
            border-bottom-right-radius: 130px;
        }
    }

    &__patty {
        height: calc(#{var(--burger-width)} / 8);
        background-color: $patty-color;
        border-radius: 33vw;
    }

    &__cheese {
        height: 15px;
        background-color: yellow;
        border-radius: 33vw;
    }

    &__lettuce {
        height: 8px;
        background-color: green;
        border-radius: 33vw;
    }

}

@media only screen and (max-device-width : 1200px) {

    .burger {
        --burger-width: 50vw;
    }

}